body {
  background-color: #2d2d2d;
  color: #d4d4d4;
  font-family: "Courier New", Courier, monospace;
  display: flex;
  justify-content: center;
  align-items: center;
  //height: 100vh;
  margin: 0;
  padding: 0;
}
fieldset {
  background-color: #1e1e1e;
  border: 2px solid #4a4a4a;
  border-radius: 10px;
  padding: 20px;
  max-width: 1024px;
  width: 100%;
  margin: 15px;
}
legend {
  padding: 5px 10px;
  font-size: 18px;
  color: #eeeeee;
  background-color: #1e1e1e;
  border: 1px solid #4a4a4a;
  border-radius: 5px;
}
.commit-history {
  display: table;
  border-spacing: 0 2px;
}
.commit {
  display: table-row;
}
.commit > span,
.commit > div {
  display: table-cell;
  padding-right: 10px;
}
.time {
  color: #6a9955;
  width: 120px;
}
.branch {
  color: #569cd6;
  width: 150px;
}
.hash {
  color: #dcdcaa;
  width: 70px;
}
.message {
  color: #d4d4d4;
}
.message p {
  margin: 0;
}
.commit p {
  line-break: auto;
}
